code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #003;
  padding-top: 50px;
  color: white;
  text-align: center;
}


.Cell {
	height: 100px;
	width: 100px;
	background-color: #263232;
	-webkit-transition: background-color 0.5s ease;
	transition: background-color 0.5s ease;
}

.Cell-lit {
	background-color: #00bcd4;
}

.Board {
	margin: 0 auto;
}

.win span {
	font-size: 10vw;
}

.title,
.win {
	display: flex;
	justify-content: center;
}

@font-face {
	font-family: neon;
	src: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/707108/neon.ttf);
}

.neon-orange {
	font-family: neon;
	color: #fb4264;
	font-size: 7vw;
	line-height: 9vw;
	text-shadow: 0 0 3vw #f40a35;
	animation: neon 1s ease infinite;
	-moz-animation: neon 1s ease infinite;
	-webkit-animation: neon 1s ease infinite;
}

.neon-blue {
	font-family: neon;
	color: #426dfb;
	font-size: 7vw;
	line-height: 9vw;
	text-shadow: 0 0 3vw #2356ff;
	animation: flux 2s linear infinite;
	-moz-animation: flux 2s linear infinite;
	-webkit-animation: flux 2s linear infinite;
	-o-animation: flux 2s linear infinite;
}

@-webkit-keyframes neon {
	0%,
	100% {
		text-shadow: 0 0 1vw #fa1c16, 0 0 3vw #fa1c16, 0 0 10vw #fa1c16, 0 0 10vw #fa1c16,
			0 0 .4vw #fed128, .5vw .5vw .1vw #806914;
		color: #fed128;
	}
	50% {
		text-shadow: 0 0 .5vw #800e0b, 0 0 1.5vw #800e0b, 0 0 5vw #800e0b, 0 0 5vw #800e0b,
			0 0 .2vw #800e0b, .5vw .5vw .1vw #40340a;
		color: #806914;
	}
}

@keyframes neon {
	0%,
	100% {
		text-shadow: 0 0 1vw #fa1c16, 0 0 3vw #fa1c16, 0 0 10vw #fa1c16, 0 0 10vw #fa1c16,
			0 0 .4vw #fed128, .5vw .5vw .1vw #806914;
		color: #fed128;
	}
	50% {
		text-shadow: 0 0 .5vw #800e0b, 0 0 1.5vw #800e0b, 0 0 5vw #800e0b, 0 0 5vw #800e0b,
			0 0 .2vw #800e0b, .5vw .5vw .1vw #40340a;
		color: #806914;
	}
}

@-webkit-keyframes flux {
	0%,
	100% {
		text-shadow: 0 0 1vw #1041ff, 0 0 3vw #1041ff, 0 0 10vw #1041ff, 0 0 10vw #1041ff,
			0 0 .4vw #8bfdfe, .5vw .5vw .1vw #147280;
		color: #28d7fe;
	}
	50% {
		text-shadow: 0 0 .5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180, 0 0 5vw #082180,
			0 0 .2vw #082180, .5vw .5vw .1vw #0a3940;
		color: #146c80;
	}
}

@keyframes flux {
	0%,
	100% {
		text-shadow: 0 0 1vw #1041ff, 0 0 3vw #1041ff, 0 0 10vw #1041ff, 0 0 10vw #1041ff,
			0 0 .4vw #8bfdfe, .5vw .5vw .1vw #147280;
		color: #28d7fe;
	}
	50% {
		text-shadow: 0 0 .5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180, 0 0 5vw #082180,
			0 0 .2vw #082180, .5vw .5vw .1vw #0a3940;
		color: #146c80;
	}
}

.App {
	width: 100vw;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
}

.App-logo {
	-webkit-animation: App-logo-spin infinite 20s linear;
	        animation: App-logo-spin infinite 20s linear;
	height: 40vmin;
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

